<template>
  <div
    class="statistics-card"
    @click="handleClick">
    <div
      class="statistics-card--title"
      :style="{
        color: `var(--color-${styleType})`,
      }">
      {{ title }}
    </div>
    <i
      class="statistics-card--icon"
      v-if="icon"
      :class="[icon]">
    </i>
    <div class="statistics-card--value">
      {{ value }}
    </div>
    <!-- <div class="statistics-card--stat" v-if="!rateClose">
      {{ conditionText }} <b>{{ Math.abs(stat) }}</b>%
    </div> -->
  </div>
</template>
<script lang='ts'>

import { computed, defineComponent, watch } from 'vue';
export default defineComponent({

  props: {
    title: {
      type: String,
    },
    value: {
      type: String,
    },
    stat: {
      type: Number,
      default: 0,
    },
    rateClose: {
      type: Boolean,
      default: false,
    },
    styleType: {
      type: String,
      default: 'primary',
    },
    icon: {
      type: String,
    },
    index: {
      type: Number,
      default: -1,
    },
  },

  setup(props, { emit }) {


    const condition = computed(() => {
      return props.stat >= 0
        ? 'fa fa-arrow-up'
        : 'fa fa-arrow-down'
    })

    const handleClick = () => {
      emit('click', props.index)
    }

    return {
      condition,
      handleClick,
      ...props,
    }

  },
})
</script>
