

import { computed, defineComponent, watch } from 'vue';
export default defineComponent({

  props: {
    title: {
      type: String,
    },
    value: {
      type: String,
    },
    stat: {
      type: Number,
      default: 0,
    },
    rateClose: {
      type: Boolean,
      default: false,
    },
    styleType: {
      type: String,
      default: 'primary',
    },
    icon: {
      type: String,
    },
    index: {
      type: Number,
      default: -1,
    },
  },

  setup(props, { emit }) {


    const condition = computed(() => {
      return props.stat >= 0
        ? 'fa fa-arrow-up'
        : 'fa fa-arrow-down'
    })

    const handleClick = () => {
      emit('click', props.index)
    }

    return {
      condition,
      handleClick,
      ...props,
    }

  },
})
